import mixins from "~/utils/mixins";
import {common} from "~/utils/api/url";
import {
  cityInfo,
  educationInfo,
  MessageError,
  MessageSuccess,
  nationalityInfo,
  nationInfo,
  provinceInfo, trainsType
} from "~/utils/utils";

export default {
  mixins: [mixins],
  data(){
    return {
      processData: [],
      tableData: [],
      tableSignData: [],
      countNum: 0,
      signNum: 0,
      unSignNum: 0,
      collegeList: [],
      categorys: [],
      types: [],
      subTitle: '',
      collegeData: '',
      searchKey: '',
      searchType: '',
      searchStatus: '',
      dialogLoading: false,
      dialogVisible: false,
      dialogDetail: false,
      visibleConfim: false,
      footerHidden: false,
      btnLoading: false,
      searchCascader: [],
      yearOptions: [],
      bathOptions: [],
      subjectOptions: [],
      filtersAuditType: [],
      searchTimeData: [],
      searchArriveTimeData: [],
      searchProcess: '',
      filtersSexType: [
        {value: 3, text: this.$t("未知")},
        {value: 1, text: this.$t("男")},
        {value: 2, text: this.$t("女")}
      ],
      filtersSignType: [
        {value: true, text: this.$t("是")},
        {value: false, text: this.$t("否")}
      ],
      filtersVehicleType: [],
      filtersInterFaceStatusType: [
        {value: true, text: this.$t("已接到")},
        {value: false, text: this.$t("未接到")},
      ],
      searchAuditType: '',
      searchSexType: '',
      searchSignTimeStatus: '',
      searchInterface: '',
      searchInterFaceStatusType: '',
      searchVehicleType: '',
      searchCollege: '',
      searchMajor: '',
      searchGrade: '',
      searchClass: '',
      form: {
        id: '',
        year: '',
        name: '',
        adNo: '',
        oneCardNo: '',
        stuNo: '',
        examNo: '',
        sex: '',
        idCardNo: '',
        birthday: '',
        nationality: '',
        nation: '',
        phone: '',
        qq: '',
        wechat: '',
        fatherName: '',
        fatherPhone: '',
        motherName: '',
        motherPhone: '',
        address: '',
        education: '',
        college: [],
        major: '',
        class: [],
        eduSystem: '',
        headmaster: '',
        recruitingTeacher: '',
        adBath: '',
        subject: '',
        adProvince: '',
        adCity: '',
        graduationSchool: '',
        examScore: '',
        otherMsg: ''
      }
    }
  },
  created() {
    this.initProcess();
    this.init();
    this.initTransType();
  },
  methods: {
    init(){
      let params = {
        page: this.page,
        num: this.num,
        arriveEstimateStartTime: (this.searchArriveTimeData && this.searchArriveTimeData.length > 0) ? this.$moment(this.searchArriveTimeData[0]).format("YYYY-MM-DD") : '',
        arriveEstimateEndTime: (this.searchArriveTimeData && this.searchArriveTimeData.length > 0) ? this.$moment(this.searchArriveTimeData[1]).format("YYYY-MM-DD") : '',
        arriveStartTime: (this.searchTimeData && this.searchTimeData.length > 0) ? this.$moment(this.searchTimeData[0]).format("YYYY-MM-DD") : '',
        arriveEndTime: (this.searchTimeData && this.searchTimeData.length > 0) ? this.$moment(this.searchTimeData[1]).format("YYYY-MM-DD") : '',
        searchKey: this.searchKey,
        collegeId: this.searchCollege,
        majorId: this.searchMajor,
        grade: this.searchGrade,
        classId: this.searchClass,
        sex: this.searchSexType,
        checkinOnTime: this.searchSignTimeStatus,
        arriveStatus: this.searchInterFaceStatusType,
        needReceive: this.searchInterface,
        processId: this.searchProcess,
        trainType: this.searchVehicleType
      };
      this.$axios.get(common.enroll_arrive_page, {params: params}).then(res => {
        if (res.data.data){
          this.tableData = res.data.data.list;
          this.total = res.data.data.totalCount;
          this.num = res.data.data.num;
          this.page = res.data.data.currentPage;
        }
      });
    },
    initProcess(){
      let params = {
        page: 1,
        num: 99999
      };
      this.$axios.get(common.enroll_process_page, {params: params}).then(res => {
        if (res.data.data){
          let process = [];
          for (let i = 0; i < res.data.data.list.length; i++){
            process.push({
              label: res.data.data.list[i].processName,
              value: res.data.data.list[i].id,
              text: res.data.data.list[i].processName,
            });
          }
          this.processData = process;
        }
      });
    },
    initTransType(){
      let params = {};
      this.$axios.get(common.enroll_link_arrive_trans_type, {params: params}).then(res => {
        if (res.data.data){
          let obj = [];
          for (let item in res.data.data){
            obj.push({value: item, text: res.data.data[item]},)
          }
          this.filtersVehicleType = obj;
        }
      });
    },
    sizeChange(event){
      this.page = 1;
      this.num = event;
      this.init();
    },
    currentPage(event){
      this.page = event;
      this.init();
    },
    jumpPage(data){
      this.page = data;
      this.init();
    },
    nodeClick(data){
      this.searchCollege = "";
      this.searchMajor = "";
      this.searchGrade = "";
      this.searchClass = "";
      if (data.unit == 1){
        this.searchCollege = data.id;
      }else if (data.unit == 2){
        this.searchCollege = data.college_id;
        this.searchMajor = data.id;
      }else if (data.unit == 3){
        this.searchMajor = data.major_id;
        this.searchGrade = data.grade;
      }else if (data.unit == 4){
        this.searchClass = data.id;
      }
      this.page = 1;
      this.init();
    },
    handleCascaderChange(data){
      this.searchCascader = data;
    },
    handleChangeSelect(data){
      this.searchProcess = data;
    },
    handleChangeTime(data, type){
      if (type == 1){
        this.searchArriveTimeData = data ? data : [];
      }else if (type == 2){
        this.searchTimeData = data ? data : [];
      }
    },
    search(data){
      this.searchKey = data.input;
      this.page = 1;
      this.init();
    },
    trainsTypeInfo(value, type){
      return trainsType(value,type);
    },
    expandInfo() {
      let url = common.enroll_arrive_export;
      let params = {
        page: 1,
        num: 99999,
        arriveStartTime: (this.searchTimeData && this.searchTimeData.length > 0) ? this.$moment(this.searchTimeData[0]).format("YYYY-MM-DD") : '',
        arriveEndTime: (this.searchTimeData && this.searchTimeData.length > 0) ? this.$moment(this.searchTimeData[1]).format("YYYY-MM-DD") : '',
        searchKey: this.searchKey,
        collegeId: this.searchCollege,
        majorId: this.searchMajor,
        grade: this.searchGrade,
        classId: this.searchClass,
        sex: this.searchSexType,
        checkinOnTime: this.searchSignTimeStatus,
        arriveStatus: this.searchInterFaceStatusType,
        needReceive: this.searchInterface,
        processId: this.searchProcess
      };
      params = this.$qs.stringify(params);
      window.open(url + "?" + params, "_self");
    },
    changeStatus(type){
      this.searchType = type.value;
      this.page = 1;
      this.init();
    },
    fliterTable(value, row, column){
      for (let item in value){
        if (item == 'auditType'){
          this.searchAuditType = value[item][0];
        }else if (item == 'sexType'){
          this.searchSexType = value[item][0];
        }else if (item == 'signType'){
          this.searchSignTimeStatus = value[item][0];
        }else if (item == 'interfaceType'){
          this.searchInterface = value[item][0];
        }else if (item == 'vehicleType'){
          this.searchVehicleType = value[item][0];
        }else if (item == 'interFaceStatusType'){
          this.searchInterFaceStatusType = value[item][0];
        }
      }
      this.init();
    },
    okStation($event, item){
      let params = {
        id: item.arrive_data_id
      };
      params = this.$qs.stringify(params);
      this.$axios.post(common.enroll_link_arrive_receive, params).then(res => {
        if (res.data.code == 200){
          this.init();
          MessageSuccess(res.data.desc);
        }else {
          MessageError(res.data.desc);
        }
      });
    }
  }
}
